import React, { useState } from "react";
import "./Header.css";
// import Fade from "../../assests/lib/react-reveal/in-and-out/Fade";

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';


import { CgSun } from "react-icons/cg/";
import { HiMoon } from "react-icons/hi";

import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from "@fortawesome/free-brands-svg-icons";


import { style } from "glamor";


function Header(props) {

    const theme = props.theme;
    const [currTheme, setCurrTheme] = useState(props.theme);

    function changeTheme() {
        if (props.theme.name === "light") {
            props.setTheme("dark");
            localStorage.setItem("theme", "dark");
            setCurrTheme("dark");
        } else {
            props.setTheme("light");
            localStorage.setItem("theme", "light");
            setCurrTheme("light");
        }
    }

    const icon = props.theme.name === "dark" ?
        <HiMoon
            strokeWidth={1}
            size={20}
            color="#A7A7A7"
        /> :
        <CgSun
            strokeWidth={1}
            size={20}
            color="#FCE570"
        />;


    let githubLink = props.theme.name === "light" ?
        style({
            color: 'rgba(0, 0, 0, 0.55) !important;',
            '.active': {
                color: 'rgba(0, 0, 0, 0.55) !important;'
            }
        }) :
        style({
            color: 'rgba(255, 255, 255, 0.55) !important;',
            '.active': {
                color: 'rgba(255, 255, 255, 0.55) !important;'
            }
        });


    return (
        <>
            <Navbar bg={props.theme.name} variant={props.theme.name}>
                <Container>
                    <Navbar.Brand>
                        <Nav.Link as={NavLink} to='/' >
                            <img alt="logo" src={require('../../assests/images/male_avatar.png')} className="logo-image" />
                            <span className="logo-name"> Omar Faruque Shamim</span>
                        </Nav.Link>
                    </Navbar.Brand>
                    <Nav className="me-auto" >
                    </Nav>
                    <Nav>
                        <Nav.Link as={NavLink} to='/Portfolio'>Portfolio</Nav.Link>
                        <Nav.Link as={NavLink} to='/articles'>Articles</Nav.Link>
                        <Nav.Link href='https://github.com/omarshamim' className={`${githubLink}`} target="_blank">
                            <FontAwesomeIcon icon={faGithub} /> GitHub
                        </Nav.Link>
                        <span onClick={changeTheme} className="theme-icon">
                            {icon}
                        </span>
                    </Nav>
                </Container>
            </Navbar>
        </>
    );

}
export default Header;
